.card {
  margin: 10px auto;
  background: var(--grad-one);
  height: 350px;
  width: 350px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* flex-direction: column; */
  color: #ffffff;
  font-size: 96px;
  font-weight: 400;
  border-radius: var(--border-radius);
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1), 0px 4px 8px rgba(0, 0, 0, 0.1),
    0px 8px 16px rgba(0, 0, 0, 0.1), 0px 16px 32px rgba(0, 0, 0, 0.1);
  position: relative;
  cursor: pointer;
  /* animated interactivity */
  transition: 250ms;
  transform-style: preserve-3d;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0))
    translateY(var(--translate-y, 0));
}

* {
      touch-action: manipulation;
  }

.disable-doubletap-to-zoom {
    touch-action: none;
}

/* when card is clicked adding .side class */
.side {
  background: var(--grad-two);
  transform: rotateY(-180deg);
}

/* card content */
.card .front,
.card .back {
  position: absolute;
  padding: 1rem;
  backface-visibility: hidden;
  text-align: center;
}

.card .front {
/*  font-family: '王漢宗中明體繁';*/
  font-family: 'frontfont';
  transform: rotateY(0deg);
}

.card.side .front {
  opacity: 0;
  display: none;
}

.card .back {
/*  font-family: '王漢宗中明體注音';*/
  font-family: 'backfont';
  transform: rotateY(180deg);
  padding-left:60px;
  padding-bottom:50px;
  z-index:2;
}

.card .back .doublefont {
  position: absolute;
  font-family: radicals;
  color: yellow;
  top: +16px;
  z-index:1;
}

.bottomalign {
  position: absolute;
  top: 80%;
  left: 50%;
  padding-left:0px;
/*  padding-bottom:0px;*/
  transform: translateX(-50%);
/*  transform: translateY(-50px);*/
  text-align: center;
  font-size: 40px;
  white-space: nowrap;
}

.bottomalign hr {
  margin-top:15px;
  margin-bottom:5px;  
}

.card small {
  opacity: 0.7;
  font-size: 0.7em;
  position: absolute;
  top: 3rem;
}

.card.side small {
  transform: rotateY(180deg);
}

.card small span {
  display: block;
  text-align: center;
  font-size: small;
}

.loading {
  color: white;
  width: 350px;
  text-align: center;
  margin: 20px auto;
}
