:root {
  --fontFamily: frontfont, serif;
  --border-radius: 20px;
  --grad-one: linear-gradient(#0702a8, #221ce8);
  --grad-two: linear-gradient(#96153e, #e02341);
  --grad-three: linear-gradient(#111111, #333333);
  --layout-width: 350px;
}

* {
      touch-action: manipulation;
  }

.disable-doubletap-to-zoom {
    touch-action: none;
}

body {
  margin: 20px auto;
  background: #242934;
  font-family: var(--fontFamily);
  color: white;
  touch-action: manipulation;

}

.nav {
  width: var(--layout-width);
  margin: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  touch-action: manipulation;

}

.nav button {
  border: none;
  background: #111122;
  color: white;
  font-size: 1.1em;
  padding: 0.8em 1.2em;
  border-radius: var(--border-radius);
  margin: 0.8em;
  opacity: 0.85;
  transition: all 0.25s;
  cursor: pointer;
  line-height: 48px;
  touch-action: manipulation;
}

.nav button.short {
  line-height: 24px;
}

.nav button:hover {
  opacity: 1;
  transform: translateY(-2px);
}

.nav button:disabled {
  cursor: not-allowed;
  opacity: 0.5;
  transform: translateY(0px);
}

.nav button:focus {
  outline: none;
}

.cardNumber {
  color: white;
  display: flex;
  justify-content: center;
}
